/**
 * CSS variables, use Sass related settings in settings.scss file.
 */
:root {
  /* colors.
   *
   * All colors related variables.
   *
   * */
  --color-grey-white: #fff;
  --color-grey-black: #111;
  --color-grey-900: #222;
  --color-grey-800: #3b3b3b;
  --color-grey-700: #515151;
  --color-grey-600: #626262;
  --color-grey-500: #7e7e7e;
  --color-grey-400: #9e9e9e;
  --color-grey-300: #b1b1b1;
  --color-grey-200: #cfcfcf;
  --color-grey-100: #e1e1e1;
  --color-grey-050: #f7f7f7;
  --color-grey-10-white: rgba(255, 255, 255, 0.1);
  --color-grey-40-white: rgba(255, 255, 255, 0.4);
  --color-grey-10-black: rgba(0, 0, 0, 0.1);
  --color-grey-30-black: rgba(0, 0, 0, 0.3);
  --color-grey-80-black: rgba(0, 0, 0, 0.8);
  --color-orange: #ec752f;
  --color-red: #ea3323;
  --color-error: #e53922;
  --color-success-700: #2f8132;
  --color-success-500: #57ae5b;
  --color-success-200: #c1eac5;
  --color-success-100: #e3f9e5;
  --color-text-primary: var(--color-grey-black);
  --color-text-secondary: var(--color-grey-600);
  --color-text-invert: var(--color-grey-10-white);
  --color-surface-primary: var(--color-grey-white);
  --color-surface-secondary: var(--color-grey-050);
  --color-surface-invert: var(--color-grey-black);
  --color-surface-invert-secondary: var(--color-grey-900);
  --color-text-error: var(--color-error);
  --color-link: var(--color-grey-black);
  --color-border-primary: var(--color-grey-300);
  --color-border-secondary: var(--color-grey-black);
  --color-border-invert-primary: var(--color-grey-700);
  --color-border-error: var(--color-error);
  --color-form-text: var(--color-grey-black);
  --color-form-border: var(--color-grey-300);
  --color-form-background: var(--color-grey-050);
  --color-radio-fill: var(--color-grey-black);
  --color-form-focus: var(--color-grey-900);
  --color-form-error: var(--color-error);
  /* Font sizes. */
  --heading-600: fluid-size(1.5rem, 4rem);
  --heading-500: fluid-size(1.5rem, 3.5rem);
  --heading-400: fluid-size(1.25rem, 2.5rem);
  --heading-300: fluid-size(1.125rem, 1.5rem);
  --heading-200: fluid-size(1rem, 1.25rem);
  --text-600: fluid-size(1.25rem, 1.5rem);
  --text-500: fluid-size(1rem, 1.25rem);
  --text-400: fluid-size(0.9375rem, 1.125rem);
  --text-300: fluid-size(0.875rem, 1rem);
  --text-static-0875rem: 0.875rem;
  --font-family-heading: "Work Sans", sans-serif;
  --font-family-base: "Inter", sans-serif;
  --font-family-secondary: "Georgia", serif;
  --line-height-heading: 1.2;
  --line-height-body: 1.5;
  /* Spacing.
   *
   * Generate new values using the following link.
   *
   * @link https://utopia.fyi/space/calculator/?c=320,16,1.2,2560,24,1.25,5,2,1920&s=0.75|0.5|0.25,1.5|2|3|5|7|10,s-3xl|m-3xl&g=s,l,xl,12
   * */
  --space-3xs: clamp(0.25rem, 0.2321rem + 0.0893vi, 0.375rem);
  --space-2xs: clamp(0.5rem, 0.4643rem + 0.1786vi, 0.75rem);
  --space-xs: clamp(0.75rem, 0.6964rem + 0.2679vi, 1.125rem);
  --space-s: clamp(1rem, 0.9286rem + 0.3571vi, 1.5rem);
  --space-m: clamp(1.5rem, 1.3929rem + 0.5357vi, 2.25rem);
  --space-l: clamp(2rem, 1.8571rem + 0.7143vi, 3rem);
  --space-xl: clamp(3rem, 2.7857rem + 1.0714vi, 4.5rem);
  --space-2xl: clamp(5rem, 4.6429rem + 1.7857vi, 7.5rem);
  --space-3xl: clamp(7rem, 6.5rem + 2.5vi, 10.5rem);
  --space-4xl: clamp(10rem, 9.2857rem + 3.5714vi, 15rem);
  /* One-up pairs */
  --space-3xs-2xs: clamp(0.25rem, 0.1786rem + 0.3571vi, 0.75rem);
  --space-2xs-xs: clamp(0.5rem, 0.4107rem + 0.4464vi, 1.125rem);
  --space-xs-s: clamp(0.75rem, 0.6429rem + 0.5357vi, 1.5rem);
  --space-s-m: clamp(1rem, 0.8214rem + 0.8929vi, 2.25rem);
  --space-m-l: clamp(1.5rem, 1.2857rem + 1.0714vi, 3rem);
  --space-l-xl: clamp(2rem, 1.6429rem + 1.7857vi, 4.5rem);
  --space-xl-2xl: clamp(3rem, 2.3571rem + 3.2143vi, 7.5rem);
  --space-2xl-3xl: clamp(5rem, 4.2143rem + 3.9286vi, 10.5rem);
  --space-3xl-4xl: clamp(7rem, 5.8571rem + 5.7143vi, 15rem);
  /* Custom pairs */
  --space-s-3xl: clamp(1rem, -0.3571rem + 6.7857vi, 10.5rem);
  --space-m-3xl: clamp(1.5rem, 0.2143rem + 6.4286vi, 10.5rem);
  /* This is added afterwards. */
  --space-button-vertical: clamp(0.375rem, 0.3036rem + 0.3571vw, 0.875rem);
  --margin-top-default: var(--space-3xl-4xl);
  --margin-top-medium: var(--space-3xl);
  --margin-top-small: var(--space-xl-2xl);
  --padding-top-default: var(--space-3xl-4xl);
  --padding-bottom-default: var(--space-3xl-4xl);
  --padding-top-small: var(--space-xl-2xl);
  --padding-bottom-small: var(--space-xl-2xl);
  --gutter: var(--space-xl);
  --side-padding: var(--space-s-3xl);
  /* Widths.
   *
   * All widths related variables.
   *
   * */
  --max-width-site: 160rem;
  --max-width-container: 160rem;
  --max-width-medium: calc(var(--max-width-container) * 0.666666666);
  --max-width-regular: calc(var(--max-width-container) * 0.5);
  --width-wide: clamp(min(50rem, 100%), 100%, var(--max-width-container));
  --width-medium: clamp(min(40rem, 100%), 66.666666666%, var(--max-width-medium));
  --width-regular: clamp(min(40rem, 100%), 50%, var(--max-width-regular));
  --width-regular--narrow: 36rem;
  /* Miscellanious.
   *
   * All miscellanious variables.
   *
   * */
  --transition-2: 0.2s;
  --transition-3: 0.3s;
  --transition-4: 0.4s;
  --transition-12: 1.2s;
  --radius-full: 65em;
  --radius-large: 1rem;
  --radius-brand: 0.5rem;
  --radius-minimal: 0.25rem;
  --radius-tiny: 0.125rem;
  --box-shadow-sm:
      0 1px 3px 0 rgba(31, 41, 55, 0.1),
      0 1px 2px 0 rgba(31, 41, 55, 0.06);
  --box-shadow-md:
      0 2px 4px -1px rgba(31, 41, 55, 0.06),
      0 4px 4px 0 rgba(0, 0, 0, 0.06);
  --box-shadow-l:
      5px -5px 15px 0 rgba(25, 26, 26, 0.08),
      -5px 5px 15px 0 rgba(25, 26, 26, 0.08);
  --box-shadow-xl:
      0 20px 25px -5px rgba(31, 41, 55, 0.1),
      0 10px 10px -5px rgba(7, 42, 200, 0.04),
      0 -2px 7px 1px rgba(7, 42, 200, 0.05);
}